@import "../common/style/sass/variabless";

.projectsBlock {
    padding: 60px 0;
    background: $bgFirstColor;
    display: flex;
    align-items: center;

    .projectsContainer {
        display: flex;
        flex-direction: column;
        align-items: center;


        .projects {
            width: 100%;
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;

            @media screen and (max-width: 477px) {
                flex-direction: column;
            }

        }
    }
    @media screen and (max-width: 477px) {
        padding: 70px 0;
    }
}


